    url("../fonts/manrope-semibold.otf") format("truetype");
  font-style: normal;
  font-weight: 600;
}
@font-face {
  font-family: "Manrope";
  src: url("../fonts/manrope-medium.woff2") format("woff2"),
    url("../fonts/manrope-medium.woff") format("woff"),
    url("../fonts/manrope-medium.otf") format("truetype");
  font-style: normal;
  font-weight: 500;
}
